<script>
import Layout from "@/layouts/main";
import appConfig from "@/app.config";
import PageHeader from "@/components/page-header";

import { required } from "vuelidate/lib/validators";

import { earningLineChart, salesAnalyticsDonutChart, ChatData, notificationData, 
  facilTurbin, facilBoiler, facilStorage } from "./data";
import { columnChart } from "./columnchart-data";
import MainFacilityStatusItem from "./main-facility-status-item";
import Activity from "./activity";
import FacilityStatusChart from './facility-status-chart.vue';
import SensorDetails from './sensor-details/sensor-details.vue';

/**
 * Saas Dashboard Component
 */
export default {
  page: {
    title: "주요설비현황",
    meta: [
      {
        name: "description",
        content: appConfig.description,
      },
    ],
  },
  components: {
    Layout,
    PageHeader,
    MainFacilityStatusItem,
    Activity,
    FacilityStatusChart,
    SensorDetails
  },
  data() {
    return {
      columnChart: columnChart,
      earningLineChart: earningLineChart,
      salesAnalyticsDonutChart: salesAnalyticsDonutChart,
      ChatData: ChatData,
      notificationData: notificationData,
      facilTurbin: facilTurbin,
      facilBoiler: facilBoiler, 
      facilStorage: facilStorage,
      title: "주요설비현황",
      items: [
        {
          text: "종합지표현황",
          href: "/",
        },
        {
          text: "주요설비현황",
          active: true,
        },
      ],
      toggleMode: "",
      selectedItemIndex:-1,
      selectedItem:"",
      submitted: false,
      chat: {
        message: "",
      },
    };
  },
  validations: {
    chat: {
      message: {
        required,
      },
    },
  },
  computed: {
    isTurbinVisible() {
      return (this.toggleMode.includes("turbin")||this.toggleMode === "")
    },
    isBoilerVisible() {
      return (this.toggleMode.includes("boiler")||this.toggleMode === "")
    },
    isStorageVisible() {
      return (this.toggleMode.includes("storage")||this.toggleMode === "")
    },
    isSensorDetailVisible() {
      return (this.toggleMode.includes("sensor"))
    },
    isChartVisible() {
      return (this.toggleMode !== "" && !this.toggleMode.includes("sensor"))
    }
  },
  methods: {
    /**
     * Char form Submit
     */
    // eslint-disable-next-line no-unused-vars
    formSubmit(e) {
      this.submitted = true;

      // stop here if form is invalid
      this.$v.$touch();

      if (this.$v.$invalid) {
        return;
      } else {
        const id = this.ChatData.length;
        const message = this.chat.message;
        const currentDate = new Date();
        this.ChatData.push({
          id: id + 1,
          align: "right",
          name: "Henry Wells",
          message,
          time: currentDate.getHours() + ":" + currentDate.getMinutes(),
        });
        this.handleScroll();
      }
      this.submitted = false;
      this.chat = {};
    },

    handleScroll() {
      if (this.$refs.current.$el) {
        setTimeout(() => {
          this.$refs.current.SimpleBar.getScrollElement().scrollTop =
            this.$refs.current.SimpleBar.getScrollElement().scrollHeight + 1500;
        }, 500);
      }
    },
    toggleClass(tagName, selId=-1, selItem='') {
      console.log("toggleClass:"+tagName+"  :"+this.toggleMode)
      this.selectedItemIndex=selId
      this.selectedItem=selItem
      if (tagName.includes("sensor")) {
          //this.toggleMode = (this.toggleMode.includes("sensor"))?tagName.replace('sensor-',''):tagName
          this.toggleMode = tagName
      } else if (tagName==="turbin") {
          this.toggleMode = (this.toggleMode==="turbin")?"":tagName
      } else if (tagName === "boiler") {
          this.toggleMode = (this.toggleMode==="boiler")?"":tagName
      } else if (tagName === "storage") {
          this.toggleMode = (this.toggleMode==="storage")?"":tagName
      }
      // } else if (tagName.includes("turbin")) {
      //     this.toggleMode = (this.toggleMode.includes("turbin"))?"":tagName
      // } else if (tagName === "boiler") {
      //     this.toggleMode = (this.toggleMode.includes("boiler"))?"":tagName
      // } else if (tagName === "storage") {
      //     this.toggleMode = (this.toggleMode.includes("storage"))?"":tagName
      // }
      //toggleClass
    }
  },
  mounted() {
    //
  },
};
</script>

<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row dashboard">
      <div class="col-lg-12">
        <div class="card">
          <div class="card-body">
            <b-tabs justified nav-class="nav-tabs-custom" content-class="p-3 text-muted">
              <b-tab active>
                <template v-slot:title>
                  <span class="d-inline-block d-sm-none">
                    <i class="fas fa-home"></i>
                  </span>
                  <span class="d-none d-sm-inline-block">9호기</span>
                </template>
                <div class="row">
                  <div class="col-xl-3 facility-toggle" v-if="isTurbinVisible">
                    <div class="card border border-info">
                      <div class="card-body">
                        <div class="clearfix">
                          <div class="float-right">
                            <a href="#" @click="toggleClass('turbin')"><i class="bx bx-line-chart text-info"></i></a>
                          </div>
                          <h4 class="card-title mb-4">{{ facilTurbin.facility}}</h4>
                        </div>
                        <div v-for="( item, index ) in facilTurbin.items" :key="index"  @click="toggleClass('sensor-turbin', index, item.item)">
                          <main-facility-status-item :id="facilTurbin.id+'_'+index" 
                          :title="item.item" 
                          :needNarrow="false"
                          :items="item.checkPoints"
                          :selected="(toggleMode==='sensor-turbin'&&selectedItemIndex==index)"
                          :alarmLevel="(item.alarmLevel)?item.alarmLevel:'muted'"></main-facility-status-item>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3 facility-toggle" v-if="isBoilerVisible">
                    <div class="card border border-info">
                      <div class="card-body">
                        <div class="clearfix">
                          <div class="float-right">
                            <a href="#" @click="toggleClass('boiler')"><i class="bx bx-line-chart text-info"></i></a>
                          </div>
                          <h4 class="card-title mb-4">{{ facilBoiler.facility}}</h4>
                        </div>
                        <div v-for="( item, index ) in facilBoiler.items" :key="index">
                          <main-facility-status-item :id="facilBoiler.id+'_'+index" 
                          :title="item.item" 
                          :items="item.checkPoints"
                          :selected="(toggleMode==='sensor-boiler'&&selectedItemIndex==index)"
                           @click="toggleClass('sensor-boiler', index, item.item)"></main-facility-status-item>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-xl-3 facility-toggle" v-if="isStorageVisible">
                    <div class="card border border-info">
                      <div class="card-body">
                        <div class="clearfix">
                          <div class="float-right">
                            <a href="#" @click="toggleClass('storage')"><i class="bx bx-line-chart text-info"></i></a>
                          </div>
                          <h4 class="card-title mb-4">{{ facilStorage.facility}}</h4>
                        </div>
                        <div v-for="( item, index ) in facilStorage.items" :key="index">
                          <main-facility-status-item :id="facilStorage.id+'_'+index" 
                          :title="item.item" 
                          :needNarrow="false"
                          :items="item.checkPoints"
                          :selected="(toggleMode==='sensor-storage'&&selectedItemIndex==index)"
                           @click="toggleClass('sensor-storage', index, item.item)"></main-facility-status-item>
                        </div>
                      </div>
                    </div>
                  </div>                      
                  <div class="col-xl-6 facility-toggle" v-if="isChartVisible">
                    <div class="card border border-info">
                      <div class="card-body">
                        <div class="clearfix">
                          <h4 class="card-title mb-4">터빈 위험율 추이</h4>
                        </div>
                        <facility-status-chart></facility-status-chart>
                      </div>
                    </div>
                    <div class="card border border-info">
                      <div class="card-body">
                        <h4 class="card-title mb-4">터빈 설비별 위험율 현황</h4>
                        <!-- Column Charts -->
                        <apexchart
                          class="apex-charts"
                          height="250"
                          type="bar"
                          dir="ltr"
                          :series="columnChart.series"
                          :options="columnChart.chartOptions"
                        ></apexchart>
                      </div>
                    </div>
                  </div>                      
                  <div class="col-xl-6 facility-toggle" v-if="isSensorDetailVisible">
                    <div class="card border border-info">
                      <div class="card-body">
                        <sensor-details :title="selectedItem"></sensor-details>
                      </div>
                    </div>
                  </div>                      
                  <div class="col-xl-3">
                    <div class="card border border-info">
                      <Activity></Activity>
                      <!-- <div class="card-body">
                        <div class="clearfix">
                          <div class="float-right">
                            <a href="#"><i class="bx bx-line-chart"></i></a>
                          </div>
                          <h4 class="card-title mb-4">9호기 이벤트발생 내역</h4>
                        </div>
                        <div class="row">
                          <Activity></Activity>
                        </div>
                      </div> -->
                    </div>
                  </div>
                </div>
              </b-tab>
              <b-tab>
                <template v-slot:title>
                  <span class="d-inline-block d-sm-none">
                    <i class="far fa-user"></i>
                  </span>
                  <span class="d-none d-sm-inline-block">10호기</span>
                </template>
                {{ content }}
              </b-tab>
            </b-tabs>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>
