<script>

/**
 * Nav-bar Component
 */
export default {
  props: {
    isActive: {
      type: Boolean,
      default: false,
    },
    logDateTime: {
      type: String, 
      required: true
    },
    logMsg: {
      type: String,
      default: ""
    },
    alarmLevel: {
      type: String,
      default:"muted"
    }
  },
  computed: {
    //
  },
  methods: {
  },
};
</script>

<template>
          <li :class="'event-list '+(isActive?'active':'')">
            <div class="event-timeline-dot" v-if="isActive">
              <i class="bx bxs-right-arrow-circle font-size-18 bx-fade-right"></i>
            </div>
            <div class="event-timeline-dot" v-else>  
              <i class="bx bx-right-arrow-circle font-size-18"></i>
            </div>
            <div class="media">
              <div class="mr-3">
                <div :class="'log-time text-'+alarmLevel">
                  {{ logDateTime }}
                  <i
                    class="bx bx-right-arrow-alt font-size-16 text-primary align-middle ml-2"
                  ></i>
                </div>
              </div>
              <div class="media-body">
                <div>
                  
                  <span :class="'log-msg text-'+alarmLevel">
                    {{ logMsg }}</span>... 
                   <a href="#">상세</a>
                </div>
              </div>
            </div>
          </li>
</template>