<script>
import {
    OveviewChart,
    activitiesData
} from "./data";
/**
 * Wallets component
 */
export default {
    components: {
        //
    },
    props: {
        title: {
            type: String, 
            required: true
        }
    },
    data() {
        return {
            OveviewChart: OveviewChart,
            activitiesData: activitiesData,
            items: [{
                    text: "Crypto",
                    href: "/",
                },
                {
                    text: "Wallets",
                    active: true,
                },
            ],
            totalRows: 1,
            currentPage: 1,
            perPage: 10,
            pageOptions: [10, 25, 50, 100],
            filter: null,
            filterOn: [],
            sortBy: "id",
            sortDesc: false,
            fields: [{
                    key: "id",
                    label:'센서No',
                    sortable: true,
                },
                {
                    key: "date",
                    label:'발생시간',
                    sortable: true,
                },
                {
                    key: "type",
                    label:'측정단위',
                    sortable: true,
                },
                {
                    key: "amount",
                    label:"측정값",
                    sortable: true,
                },
            ],
        };
    },
    computed: {
        /**
         * Total no. of records
         */
        rows() {
            return this.activitiesData.length;
        },
    },
    mounted() {
        // Set the initial number of items
        this.totalRows = this.items.length;
    },
    methods: {
        /**
         * Search the table data with search input
         */
        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },
    },
};
</script>

<template>
<Layout>
    <div class="row">

        <div class="col-xl-12">

            <div class="card">
                <div class="card-body">
                    <h4 class="card-title mb-3">센서 현황 ({{ title }})</h4>

                    <div>
                        <div id="overview-chart" class="apex-charts" dir="ltr"></div>
                        <apexchart class="apex-charts" height="240" type="line" dir="ltr" :series="OveviewChart.series" :options="OveviewChart.chartOptions"></apexchart>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!-- end row -->
    <div class="row">
        <div class="col-lg-12">
            <div class="card">
                <div class="card-body">
                    <h4 class="card-title mb-4">센서 상세내역 ({{ title }})</h4>
                    <b-tabs content-class="mt-3" nav-class="nav-tabs-custom">
                        <b-tab title="설비별" active>
                            <div class="row mt-4">
                                <div class="col-sm-12 col-md-6">
                                    <div id="tickets-table_length" class="dataTables_length">
                                        <label class="d-inline-flex align-items-center font-size-12">
                                            &nbsp;
                                            <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;<span style="white-space:pre;">건씩</span>
                                        </label>
                                    </div>
                                </div>
                                <!-- Search -->
                                <div class="col-sm-12 col-md-6">
                                    <div id="tickets-table_filter" class="dataTables_filter text-md-right  align-items-center">
                                        <label class="d-inline-flex align-items-center">
                                            <span style="white-space:pre;">검색:</span>
                                            <b-form-input v-model="filter" type="search" placeholder="Search..." class="form-control form-control-sm ml-2"></b-form-input>
                                        </label>
                                    </div>
                                </div>
                                <!-- End search -->
                            </div>
                            <!-- Table -->
                            <div class="table-responsive table-hover mb-0">
                                <b-table :items="activitiesData" :fields="fields" responsive="sm" :per-page="perPage" :current-page="currentPage" :sort-by.sync="sortBy" :sort-desc.sync="sortDesc" :filter="filter" :filter-included-fields="filterOn" @filtered="onFiltered"></b-table>
                            </div>
                            <div class="row">
                                <div class="col">
                                    <div class="dataTables_paginate paging_simple_numbers float-right">
                                        <ul class="pagination pagination-rounded mb-0">
                                            <!-- pagination -->
                                            <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </b-tab>
                        <b-tab title="IoT감지기" disabled> </b-tab>
                    </b-tabs>
                </div>
            </div>
        </div>
    </div>
</Layout>
</template>
